import React, { Component } from 'react';

import Errors from "./../messageComponent/messages.js";

class Login extends Component {
  

    constructor(props){
        super(props);
        this.state = {username: '', password: '', errors: []};

        this.handleSubmit = this.handleSubmit.bind(this);
        this.handleChange = this.handleChange.bind(this);
    }


    handleChange(event){
        this.setState({
            [event.target.name]: event.target.value
        });
    }

    handleSubmit(event){
        event.preventDefault();
        this.setState({errors: []});

        var data = {
        username: this.state.username,
        password: this.state.password};

        fetch('/loginsend', {
            method: 'post',
            body: JSON.stringify(data),
            credentials: 'include',
            headers: {
            "Content-Type": "application/json"}
        }).then(response => response.json())
        .then(json => {
            this.setState({errors: json.errors, messages: json.messages, successes: json.successes})
            if (json.successes && json.successes.length > 0){
                window.location.reload();
            }
        });

    }


    render() {
     
    return (
        <div id="loginModal" className="modal fade in">
            <div className="modal-dialog" role="document">
                <div id="loginModalInside" className="modal-content">
                <div className="modal-header">
                    <h5 className="modal-title">Login to Re:Market</h5>
                </div>

                    <form onSubmit={this.handleSubmit}>

                        <div className="form-group">
                            <label for="exampleInputEmail1">Username</label>
                            <input type="text" onChange={this.handleChange} className="form-control" name="username" aria-describedby="emailHelp" placeholder="Username"/>
                        </div>

                        <div className="form-group">
                            <label for="exampleInputPassword1">Password</label>
                            <input type="password" onChange={this.handleChange} className="form-control" name="password" placeholder="Password"/>
                        </div>
                          <Errors errors={this.state.errors} messages={this.state.messages} successes={this.state.successes}/>
                        <button type="submit" className="btn btn-primary">Login</button>
                    </form>
                </div>
            </div>
        </div>
    );
 
  }
}

export default Login;



