import React, { Component } from 'react';

class Messages extends Component {

    constructor(props){
        super(props);
    }

    render() {
        var errorList = []

        if (this.props.errors){
            this.props.errors.forEach(error => {
                errorList.push(<div className="alert alert-danger">{error.msg}</div>)
            })
        }

        var messageList = []

        if (this.props.messages) {
            this.props.messages.forEach(message => {
                messageList.push(<div className="alert alert-primary">{message.msg}</div>)
            })
        }

        var successList = []

        if (this.props.successes) {
            this.props.successes.forEach(message => {
                messageList.push(<div className="alert alert-success">{message.msg}</div>)
            })
        }

        return <div>{errorList}{messageList}{successList}</div>
    }
}

export default Messages;



