import React, { Component } from 'react';

import Errors from "./../messageComponent/messages.js";

class Register extends Component {

    constructor(props){
        super(props);
        this.state = {username: '', password: '', passconf: '', errors: []};

        this.handleSubmit = this.handleSubmit.bind(this);
        this.handleChange = this.handleChange.bind(this);
    }


    handleChange(event){
        this.setState({
            [event.target.name]: event.target.value
        });
    }

    handleSubmit(event){
        event.preventDefault();
        this.setState({errors: []});

        var data = {
        username : this.state.username,
        password : this.state.password,
        passconf : this.state.passconf};

        console.log(data);

        fetch('/registersend', {
            method: 'post',
            body: JSON.stringify(data),
            headers: {
            "Content-Type": "application/json"}
        }).then(response => response.json())
        .then(json => {   
            this.setState({errors: json.errors, messages: json.messages, successes: json.successes})
        
            var data = {
            username: this.state.username,
            password: this.state.password};

            fetch('/loginsend', {
                method: 'post',
                body: JSON.stringify(data),
                credentials: 'include',
                headers: {
                "Content-Type": "application/json"}
            }).then(response => response.json())
            .then(json => {
                this.setState({errors: json.errors, messages: json.messages, successes: json.successes})
                if (json.successes && json.successes.length > 0){
                    window.location.reload();
                }
            });
        });
    }

    render() {
    
        return (
            <div id="registerModal" className="modal fade in">
                <div className="modal-dialog" role="document">
                    <div id="registerModalInside" className="modal-content">
                    <div className="modal-header">
                        <h5 className="modal-title">Register for Re:Market</h5>
                    </div>
                        
                        <form id="registerform" onSubmit={this.handleSubmit}>
                            <div className="form-group">
                                <label for="usernameInput">Username</label>
                                <input type="text" onChange={this.handleChange} className="form-control" id="usernameInput" name="username" placeholder="Username"/>
                            </div>

                            <div className="form-group">
                                <label for="password1">Password</label>
                                <input type="password" onChange={this.handleChange} className="form-control" id="password1" name="password" placeholder="Password"/>
                            </div>

                            <div className="form-group">
                                <label for="password2">Password confirmation</label>
                                <input type="password" onChange={this.handleChange} className="form-control" id="password2" name="passconf" placeholder="Password confirmation"/>
                            </div>

                            <div className="alert alert-warning">
                                Re:Market currently operates without permission from WarpPortal meaning <b>inventory bots may be banned</b>. If bots are banned compensation for lost items or zeny is very unlikely. 
                                <br/><br/><b> Items/zeny are stored here at your own risk.</b>
                            </div>

                            <Errors errors={this.state.errors} messages={this.state.messages} successes={this.state.successes}/>
                            <button type="submit" className="btn btn-primary">I understand</button>
                        </form>
                    </div>
                </div>
            </div>
        );
 
  }
}

export default Register;



