import React, { Component } from 'react';

import Errors from "./../messageComponent/messages.js";
import Item from "./item.js";

class Inventory extends Component {

    constructor(){
        super();
        this.state = {errors : [], messages : [], successes:[], characters:[], items:[]};
    }

    componentDidMount(){
       fetch('/characterfetch', {      
            credentials: 'include'})
        .then(res => res.json())
        .then(chars => this.setState({characters: chars.data}));

        fetch('/inventoryfetch', {      
            credentials: 'include'})
        .then(res => res.json())
        .then(items => this.setState({items: items.data}));
    };

   render() {

        var charList = []
       
        this.state.characters.forEach(char => {
            if (char.verified) {
                charList.push(<option>{char.name}</option>)
            }
        })

        var itemList = []
       
        this.state.items.forEach(item => {
            itemList.push(<Item data={item}/>)
        })

        return (
            <div className="container">
                <div className="row">
                    <div className="col">
                        <h4>Your inventory</h4>
                        <ul className="list-group">
                            {itemList}
                        </ul>
                    </div>
                    <div className="col">
                        <h4><br/></h4>
                        <ul className="list-group">

                        </ul>
                    </div>
                    <div className="col-sm-6">
                        <div className="card">
                            <div className="card-body">
                                <h4 className="card-title">Withdraw</h4>
                                <p className="card-text">
                                </p>
                                
                            <form id="addcharacterform" onSubmit={this.handleSubmit}>
                            <div className="form-group">

                                <label for="character">Character to deliver to</label>
                                <select className="form-control" id="characterselection">
                                    {charList}
                                </select>
                            </div>
                            <div className="form-group">
                                <label for="items">Items:</label>
                                <div className="container-fluid" id="inventwithdrawlist">

                                </div>
                            </div>
                            <div className="form-group">
                                <input type="text" onChange={this.handleChange} className="form-control" id="zenyInput" name="zeny" placeholder="zeny"/>
                            </div>

                            <div className="alert alert-warning">
                                There is a tax associated with withdrawals to cover the cost of transactions. Currently this is 3% on zeny and 2500z per item slot withdrawn.
                            </div>

                            <Errors errors={this.state.errors} messages={this.state.messages} successes={this.state.successes}/>
                            <button type="submit" className="btn btn-primary">Withdraw</button>
                        </form>

                            </div>
                        </div>
                    </div>
                </div>
            </div>
        );
  }
}

export default Inventory;



