import React, { Component } from 'react';
import { BrowserRouter, Route, Link } from 'react-router-dom'

import Header from './components/headerComponent/header';
import Login from './components/loginComponent/login';
import Register from './components/loginComponent/register';
import Market from './components/marketComponent/market';
import Auction from './components/auctionComponent/auction';
import Inventory from './components/inventoryComponent/inventory';
import Characters from './components/charactersComponent/characters';

import './assets/css/default.min.css';

class App extends Component {
  render() {
    return (
      <BrowserRouter >
        <Route path="/">
          <div className="App">

            <Header/>
            <Login/>
            <Register/>
            
            <Route path="/market" component={Market}/>
            <Route path="/auction" component={Auction}/>
            <Route path="/inventory" component={Inventory}/>
            <Route path="/characters" component={Characters}/>
          </div>
          </Route>
      </BrowserRouter>
    );
  }
}

export default App;



