import React, { Component } from 'react';

import Errors from "./../messageComponent/messages.js";

class Characters extends Component {

    constructor(){
        super();
        this.state = {errors: [], messages: [], successes: [], characters: []}

        this.handleSubmit = this.handleSubmit.bind(this);
        this.handleChange = this.handleChange.bind(this);
    }

    componentDidMount(){
       fetch('/characterfetch', {      
            credentials: 'include'})
        .then(res => res.json())
        .then(chars => this.setState({characters: chars.data}));
    };


    handleChange(event){
        this.setState({
            [event.target.name]: event.target.value
        });
    }

    handleSubmit(event){
        event.preventDefault();
        this.setState({errors: []});

        var data = {username: this.state.username};

        fetch('/characterfetch/charadd', {
            method: 'post',
            body: JSON.stringify(data),
            credentials: 'include',
            headers: {
            "Content-Type": "application/json"}
        }).then(response => response.json())
        .then(json => {
            this.setState({errors: json.errors, messages: json.messages, successes: json.successes})
            if (json.successes && json.successes.length > 0){
                this.componentDidMount();
            }
        });

    }


    render() {

        var charList = []
       
        this.state.characters.forEach(char => {
            if (char.verified) {
                charList.push(<li className="list-group-item list-group-item-success">{char.name}</li>)
            } else {
                charList.push(<li className="list-group-item list-group-item-warning">{char.name} (Not verified, your verifiction code is {char.code})</li>)
            }
        })

        return (
            <div className="container">
                <div className="row">
                    <div className="col">
                        <h4>Your characters</h4>
                        <ul className="list-group">
                            {charList}
                        </ul>
                    </div>
                    <div className="col-sm-6">
                        <div className="card">
                            <div className="card-body">
                                <h4 className="card-title">Add a new character</h4>
                                <p className="card-text">
                                    To add a new character enter in its exact name below. 
                                    <br/><br/>
                                    You will then be given a 5 digit verification code which you will need to send to 'ReMarket' (c-code 603-473) via rodex. 
                                    <br/><br/>
                                    Once our system has received this code we will send you confirmation and your character will be available for transactions.
                                </p>
                                
                            <form id="addcharacterform" onSubmit={this.handleSubmit}>
                            <div className="form-group">
                                <input type="text" onChange={this.handleChange} className="form-control" id="usernameInput" name="username" placeholder="Username"/>
                            </div>

                            <div className="alert alert-warning">
                                The content of the mail should <b>only contain this 5 digit code</b>.
                            </div>

                            <Errors errors={this.state.errors} messages={this.state.messages} successes={this.state.successes}/>
                            <button type="submit" className="btn btn-primary">Add character</button>
                        </form>

                            </div>
                        </div>
                    </div>
                </div>
            </div>
        );
  }
}

export default Characters;



