import React, { Component } from 'react';


import Balance from "./balance";

class Header extends Component {
  render() {
    return (
      <header>
        <div className="container">
          
          <nav className="navbar navbar-expand-lg navbar-dark bg-dark">

            <a className="navbar-brand">Re:Market</a>

            <ul className="nav nav-pills mr-auto" id="pills-tab" role="tablist">
              <li className="nav-item">
                <a className="nav-link" href="/market" onClick={() => window.location.assign("/market")}>Market</a>
              </li>
              <li className="nav-item">
                <a className="nav-link" href="/auction" onClick={() => window.location.assign("/auction")}>Auction House</a>
              </li>
              <li className="nav-item">
                <a className="nav-link" href="/inventory" onClick={() => window.location.assign("/inventory")}>Inventory</a>
              </li>
              <li className="nav-item dropdown">
                <a className="nav-link dropdown-toggle" data-toggle="dropdown" href="#settings" role="button">Settings</a>
                <div className="dropdown-menu">
                  <a className="dropdown-item" href="/characters" onClick={() => window.location.assign("/characters")} role="tab">Charcters</a>
                  <a className="dropdown-item" href="/history" onClick={() => window.location.assign("/history")} role="tab">Transaction History</a>
                </div>
              </li>
            </ul>


            <Balance/>
          </nav>
        </div>
      </header>
    );
  }
}

export default Header;



