import React, { Component } from 'react';

class Item extends Component {

    constructor(){
        super();

        fetch("http://ratemyserver.net/index.php?page=re_item_db&item_id=16770", {mode: 'no-cors'})
        .then(res => console.log(res));
    }

    render() {
        var imagesrc = "http://imgs.ratemyserver.net/items/small/"+this.props.data.id+".gif"

        return(
            <button type="button" className="btn btn-outline-secondary itembutton text-left">
                <img src={imagesrc}/>    
                {this.props.data.name}
                <span className="badge badge-pill badge-secondary">{this.props.data.amount}</span>
            </button> 
        ) 
  }
}

export default Item;



