import React, { Component } from 'react';

class Balance extends Component {

    constructor(){
        super();
        this.state = {balance: -1};
    }

    componentDidMount(){
       fetch('/balance', {      
            credentials: 'include'})
        .then(res => res.json())
        .then(state => this.setState(state));
    };

    render() {
        if (this.state.balance == -1){
            return (
                <div>
                    <button type="button" className="btn btn-dark" data-toggle="modal" data-target="#loginModal">Login</button> 
                    <button type="button" className="btn btn-dark" data-toggle="modal" data-target="#registerModal">Register</button> 
                </div>
            );
        } else {
            return (
                <span className="navbar-text">Balance: {this.state.balance.toLocaleString()}z</span>
            );
        }
  }
}

export default Balance;



